}

.watermark {
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(49%);
  font-size: 7.0833333333vw;
  white-space: nowrap;
  transition: transform 4s ease-out;
  color: #EEE9E2;
  z-index: -1;
}
.watermark p {
  font-family: "GoudyTitlingRegular";
  text-transform: uppercase;
}
@media (max-width: 1420px) {
  .watermark {
    font-size: 100px;
    top: 16px;
  }
}
