.video__bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.video__bg::before,
.video__bg video {
  position: absolute;
  width: 100%;
  height: 100%;
}
.video__bg::before {
  content: "";
  background: black;
  opacity: 0.7;
  z-index: 4;
}
.video__bg video {
  z-index: 3;
  aspect-ratio: initial;
  object-fit: cover;
}

